@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&display=swap);
.Game__progress{height:10px;border-radius:30px;background-color:#e8e8e8;margin-bottom:25px}.Game__inner{height:100%;border-radius:30px;width:80%;background:#12e730;transition:all 0.3s ease-in-out;background:linear-gradient(90deg, #12e730 0%, #00d4ff 100%)}.Game__question{margin:0}.Game__list{list-style:none;padding:0}.Game__li{padding:15px;border:2px solid rgba(0,0,0,0.1);border-radius:15px;margin-bottom:10px;cursor:pointer;transition:all 0.1s ease-in-out}.Game__li:hover{border:2px solid rgba(0,0,0,0.3)}

.Result{text-align:center}.Result__img{width:150px}.Result__btn{font-weight:bold;font-family:'Nunito', sans-serif;border-radius:30px;border:0;padding:15px 30px;font-size:20px;background-color:#f00067;color:#fff;margin-top:20px;cursor:pointer}.Result__info{margin-bottom:0}

body{margin:0;font-family:'Nunito', sans-serif;background:#a5c322;background:linear-gradient(0deg, #a5c322 0%, rgba(45,98,253,0.717822) 100%);display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh}.App{width:500px;border-radius:30px;padding:40px;background-color:#fff;position:relative;box-shadow:12px 7px 15px 5px rgba(0,0,0,0.3)}

